Vue.mixin({
  data() {
    return {
      permissions: [],
      roleUser: "",
    };
  },
  mounted() {
    // get permissions
    const permissions = JSON.parse(
      localStorage.getItem("sw_auth_data")
    ).permissions;
    permissions.forEach((item) => {
      // if (item.name == "hrm") {
      this.permissions.push(item.name);
      // }
    });

    const roles = JSON.parse(
      localStorage.getItem("sw_auth_data")
    ).roles;
    roles.forEach((item) => {
      this.roleUser = item.name;

      // push permission from role to permissions
      item.permissions.forEach((item) => {
        this.permissions.push(item.name);
      }
      );
    });
  },
  methods: {
    hasPermission(permission) {
      return this.permissions.includes(permission);
    },
    getAllPermissions() {
      return this.permissions;
    },
    getRole() {
      return this.roleUser;
    },
    capitalizeFirstLetter: str => str.charAt(0).toUpperCase() + str.slice(1),
    setCookie(name, value, days) {
      var expires = "";
      if (days) {
        var date = new Date();
        date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
        expires = "; expires=" + date.toUTCString();
      }
      document.cookie = name + "=" + (value || "") + expires + "; path=/";
    },
    getCookie(name) {
      var nameEQ = name + "=";
      var ca = document.cookie.split(';');
      for (var i = 0; i < ca.length; i++) {
        var c = ca[i];
        while (c.charAt(0) == ' ') c = c.substring(1, c.length);
        if (c.indexOf(nameEQ) == 0) return c.substring(nameEQ.length, c.length);
      }
      return null;
    },
    eraseCookie(name) {
      document.cookie = name + '=; Max-Age=-99999999;';
    },

    // axios request
    callApi(config = {}) {
      const defaultConfig = {
        method: 'GET',
        baseURL: 'https://sw-api.hracademy.id/api',
        headers: {
          Authorization: `Bearer ${this.getCookie('sw_auth_token')}`,
        },
        params: {},
        data: {},
        timeout: 10000,
      };

      axios({ ...defaultConfig, ...config })

        // set callback
        .then((response) => {
          if (config.success) {
            config.success(response.data);
          }
        })

        // set error callback
        .catch((error) => {
          if (config.error) {
            config.error(error);
          }
        })
    },

    truncateText(text, length) {
      if (text.length > length) {
        return text.substring(0, length) + '...';
      } else {
        return text;
      }
    },

    hashid(id) {
      const hashids = new Hashids("id-hra-2023", 10);
      return hashids.encode(id);
    }
  }
})

